"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Plus, Trash2 } from "lucide-react";
import TodoItem from "./components/TodoItem";

type Todo = {
  id: number;
  title: string;
  completed: boolean;
};

export default function Home() {
  const [todos, setTodos] = useState<Todo[]>([]);
  const [title, setTitle] = useState("");

  const addTodo = (e: React.FormEvent) => {
    e.preventDefault();
    if (!title.trim()) return;

    const newTodo: Todo = {
      id: Date.now(),
      title: title.trim(),
      completed: false,
    };
    setTodos([newTodo, ...todos]);
    setTitle("");
  };

  const toggleComplete = (id: number) => {
    setTodos((prev) =>
      prev.map((todo) =>
        todo.id === id ? { ...todo, completed: !todo.completed } : todo
      )
    );
  };

  const deleteTodo = (id: number) => {
    setTodos((prev) => prev.filter((todo) => todo.id !== id));
  };

  return (
    <main className="min-h-screen flex items-center justify-center px-6 bg-gradient-to-tr from-cyan-400 via-blue-500 to-purple-600 relative overflow-hidden">
      {/* Fondo animado */}
      <div className="absolute inset-0 -z-10 bg-gradient-to-r from-teal-300 via-cyan-400 to-blue-500 animate-gradient-x"></div>

      <div className="w-full max-w-3xl bg-white/20 backdrop-blur-xl rounded-3xl p-8 shadow-xl border border-white/30">
        <h1 className="text-4xl font-extrabold text-white text-center mb-8 drop-shadow-lg tracking-wide">
          ðŸŒˆ To-Do Vibrante
        </h1>

        <form onSubmit={addTodo} className="flex items-center gap-4 mb-8">
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder="Â¿QuÃ© quieres hacer hoy?"
            className="flex-grow px-5 py-4 rounded-2xl bg-white/90 text-gray-900 font-semibold placeholder-gray-500 shadow-md focus:outline-none focus:ring-4 focus:ring-cyan-300 transition"
          />
          <button
            type="submit"
            className="bg-cyan-500 hover:bg-cyan-600 text-white p-4 rounded-2xl shadow-lg transition active:scale-95 flex items-center justify-center"
          >
            <Plus className="w-6 h-6" />
          </button>
        </form>

        <ul className="space-y-4 max-h-[60vh] overflow-y-auto scrollbar-thin scrollbar-thumb-cyan-400 scrollbar-track-transparent">
          <AnimatePresence>
            {todos.map((todo) => (
              <motion.li
                key={todo.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: 30 }}
                transition={{ duration: 0.25 }}
              >
                <TodoItem
                  todo={todo}
                  onToggle={toggleComplete}
                  onDelete={deleteTodo}
                />
              </motion.li>
            ))}
          </AnimatePresence>
        </ul>
      </div>
    </main>
  );
}
